.brand-sales {
  padding: 2rem 0;
  background: #f8f9fa;

  .section-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
    padding: 0 1rem;

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;
      padding: 0 12px; // 최소 패딩
    }

    .section-title {
      font-size: 2rem;
      font-weight: bold;
      color: #333;
      margin: 0;

      @media (max-width: 768px) {
        font-size: 1.75rem;
      }
    }

    .section-subtitle {
      font-size: 1rem;
      color: #666;
      margin: 0.5rem 0 0 0;

      @media (max-width: 768px) {
        margin: 0;
      }
    }

    .more-btn {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      background: #667eea;
      color: white;
      border: none;
      padding: 0.75rem 1.5rem;
      border-radius: 25px;
      cursor: pointer;
      transition: all 0.3s ease;
      font-weight: 500;

      &:hover {
        background: #5a6fd8;
        transform: translateY(-1px);
      }

      mat-icon {
        font-size: 1.125rem;
        transition: transform 0.3s ease;
      }

      &:hover mat-icon {
        transform: translateX(2px);
      }
    }
  }

  .stores-container {
    padding: 0 1rem;
    
    @media (max-width: 768px) {
      padding: 0 8px; // 모바일에서 최소 패딩
    }
  }

  .stores-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 1.5rem;
    max-width: 1200px;
    margin: 0 auto;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 12px; // 간격 축소
    }

    @media (max-width: 480px) {
      grid-template-columns: 1fr;
    }
  }

  .store-card {
    position: relative;
    background: white;
    border-radius: 16px;
    overflow: hidden;
    transition: all 0.3s ease;
    cursor: pointer;
    border: 1px solid #e0e0e0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 4px;
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      transform: scaleX(0);
      transition: transform 0.3s ease;
      z-index: 1;
    }

    &:hover {
      transform: translateY(-4px);
      box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
      border-color: #667eea;

      &::before {
        transform: scaleX(1);
      }

      .store-logo {
        transform: scale(1.05);
      }
    }

    &:active {
      transform: translateY(-2px);
    }

    .store-card-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.5rem 1.5rem 1rem;

      .store-logo {
        font-size: 3rem;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #f5f5f5;
        border-radius: 16px;
        border: 2px solid #e0e0e0;
        transition: transform 0.3s ease;
      }

      .store-badges {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        align-items: flex-end;

        .official-badge,
        .popular-badge {
          padding: 0.25rem 0.75rem;
          border-radius: 12px;
          font-size: 0.75rem;
          font-weight: bold;
          text-transform: uppercase;
        }

        .official-badge {
          background: #4caf50;
          color: white;
        }

        .popular-badge {
          background: #ff9800;
          color: white;
        }
      }
    }

    .store-info {
      padding: 0 1.5rem 1rem;

      .store-name {
        font-size: 1.25rem;
        font-weight: bold;
        color: #333;
        margin: 0 0 0.5rem 0;
      }

      .store-description {
        font-size: 0.9rem;
        color: #666;
        margin: 0 0 1rem 0;
        line-height: 1.4;
      }

      .store-stats {
        margin-bottom: 1rem;

        .store-info-item {
          display: flex;
          justify-content: space-between;
          margin-bottom: 0.5rem;
          font-size: 0.9rem;

          .label {
            color: #666;
            font-weight: 500;
          }

          .value {
            color: #333;
          }
        }

        .product-count {
          font-size: 0.85rem;
          color: #666;
        }

        .contact-info {
          .phone {
            font-size: 0.9rem;
            color: #667eea;
            font-weight: 500;
          }
        }
      }

      .discount-info {
        .discount-rate {
          background: linear-gradient(135deg, #ff6b6b, #ffa726);
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 20px;
          font-size: 0.9rem;
          font-weight: bold;
          display: inline-block;
        }
      }
    }

    .store-card-footer {
      padding: 0 1.5rem 1.5rem;

      .visit-store-btn {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.5rem;
        background: #667eea;
        color: white;
        border: none;
        padding: 0.875rem 1rem;
        border-radius: 12px;
        cursor: pointer;
        transition: all 0.3s ease;
        font-weight: 500;

        &:hover {
          background: #5a6fd8;
          transform: translateY(-1px);
        }

        mat-icon {
          font-size: 1.125rem;
        }
      }
    }
  }

  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4rem 2rem;
    text-align: center;

    .loading-spinner {
      width: 40px;
      height: 40px;
      border: 4px solid #f3f3f3;
      border-top: 4px solid #667eea;
      border-radius: 50%;
      animation: spin 1s linear infinite;
      margin-bottom: 1rem;
    }

    p {
      color: #666;
      font-size: 1rem;
      margin: 0;
    }
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}